.todo-list-item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.todo-list-item.done {
    text-decoration: line-through;
}

.todo-list-item.important .todo-list-item-label {
    font-weight: 700;
    color: steelblue;
}